          panel:false,
          sub_genre:[
            {genre:"",url:"message"},
            {genre:"",url:"recommend"},
            {genre:"",url:"information"}
          ]
        }
      ]
    }
  },
  methods: {
    changeroute () {
      document.body.scrollTop = 0
      document.documentElement.scrollTop = 0
      this.$router.push('/')
    },
    changeroute2(url){
      document.body.scrollTop = 0
      document.documentElement.scrollTop = 0
      this.$router.push('/home/' + url)
    },
    search() {
      let self = this
       self.$router.push('/search/'+ self.productName)
    }
  },
  beforeMount() {
    let self = this
    let token = localStorage.getItem('token')
    api.getAccount(token).then(res=>{
        self.loginFlag=true;
      }).catch(error=>{
        self.loginFlag=false;
      })
  }
}
</script>

<style>

.btntop{ 
  position: relative;
  width: 100%;
  z-index: 10;
}

.ad{
  cursor: pointer;
}

.logo{
  cursor: pointer;
}

.pointer {cursor: pointer;}

</style>



