<template>
  <v-container>
      <v-layout column="">
              <v-btn class="left" color="light-blue lighten-5" block ripple=""  align="center" v-bind:key=item.genre v-for="item in toolbar(path)" @click="changeroute(item.url)"><b>{{ item.genre }}</b></v-btn>
      </v-layout>
  </v-container>
</template>

<script>
export default {
    props: ['path'],
    data(){
        return {
        }
    },
    methods: {
        toolbar: function (path) {
            if (path == 'about' || path=='introduction' || path=='history' || path=='envirnoment' || path=='records' || path=='license' || path=='report' || path=='product_report') {
                return [
                   {genre:"",url:"introduction"},
                   {genre:"",url:"history"},
                   {genre:"",url:"envirnoment"},
                   {genre:"",url:"records"},
                   {genre:"/",url:"license"},
                   {genre:"",url:"product_report"},
                   {genre:"",url:"report"},
                   {genre:"",url:"relation"}]
            }
            if(path=='catalog' || path=='download'  || path=='newGoods' || path=='pointer_pressure' || path=='pressure_switch' || path=='pressure_transmitter' || path=='transmitter' || path=='digit_pressure' ||  path=='pointer_thermometer' ||  path=='temperature_switch' || path=='digit_thermometer' || path=='TC_dipole' || path=='RTD_resistance' || path=='glass_thermometer' || path=='septum' || path=='vertify' || path=='accessory' || path=='search' || path=='temperature_transmitter' || path == 'mechanical_switch')
            {
                return[
                    {genre:"",url:"newGoods"},
                    {genre:"",url:"pointer_pressure"},
                    {genre:"",url:"pressure_switch"},
                    {genre:"()",url:"pressure_transmitter"},
                     {genre:"",url:"mechanical_switch"},
                    {genre:"",url:"transmitter"},
                    {genre:"",url:"digit_pressure"},
                    {genre:"",url:"pointer_thermometer"},
                    {genre:"",url:"temperature_switch"},
                    {genre:"()",url:"temperature_transmitter"},
                    {genre:"",url:"digit_thermometer"},
                    {genre:"TC",url:"TC_dipole"},
                    {genre:"RTD",url:"RTD_resistance"},
                    {genre:"",url:"glass_thermometer"},
                    {genre:"",url:"septum"},
                    {genre:"",url:"vertify"},
                    {genre:"",url: "accessory"},
                    {genre:"",url:"download"}]
            }
            if(path=='FRL'||path=='valve'||path=='liquid'||path=='flow'||path=='adjuster'||path=='load_transmitter')
            {
                return [
                    {genre:"F.R.L 1~3",url:"FRL"},
                    {genre:"",url:"valve"},
                    {genre:"",url:"liquid"},
                    {genre:"",url:"flow"},
                    {genre:"",url:"adjuster"},
                    {genre:"",url:"load_transmitter"}]
            }
            if(path=='industry' || path=='construction' || path=='manufacture' || path=='electricity' )
            {
                return [
                    {genre:"",url:"construction"},
                    {genre:"",url:"manufacture"},
                    {genre:"",url:"electricity"}]
            }
            if(path=='news' || path== 'lastest' || path=='DM')
            {
                return [
                    {genre:"",url:"lastest"},
                    {genre:"E-DM",url:"DM"},
                    {genre:"",url:"newGoods"}]
            }
            if(path=='technology' || path == 'instruction'|| path=='maintain' || path=='FAQ' || path=='note' || path== 'theory' || path=='skill' || path=='conversion' || path=='data'  || path== 't_link' || path=='issue')
            {
                return [
                    {genre:"",url:"instruction"},
                    {genre:"FAQ",url:"FAQ"},
                    {genre:"",url:"note"},
                    {genre:"",url:"theory"},
                    {genre:"",url:"skill"},
                    {genre:"/",url:"conversion"},
                    {genre:"/",url:"data"},
                    {genre:"",url:"t_link"},
                    {genre:"",url:"issue"}]
            }
            if(path=='scene'|| path=='equipment' || path=='film' || path=='picture')
            {
                return [
                    {genre:"/",url:"equipment"},
                    {genre:"",url:"film"},
                    {genre:"",url:"picture"}]
            }
            if(path=='link' || path=='relation' || path=='friend' || path=='national' || path=='develop' || path=='governoment' || path=='source'||path=='commerce'||path=='work'||path=='rest'||path=='life'||path=='enterprise' || path=='association')
            {
                return [
                    {genre:"",url:"relation"},
                    {genre:"",url:"friend"},
                    {genre:"",url:"national"},
                    {genre:"",url:"develop"},
                    {genre:"",url:"governoment"},
                    {genre:"",url:"source"},
                    {genre:"",url:"commerce"},
                    {genre:"/",url:"work"},
                    {genre:"",url:"rest"},
                    {genre:"",url:"life"},
                    {genre:"",url:"enterprise"},
                    {genre:"",url:"association"}]
            }
            if(path=='contact' || path=='message' || path=='information')
            {
                return [
                    {genre:"",url:"message"},
                    {genre:"",url:"information"}
                ]
            }
            if(path=='member' || path=='update' || path=='order' || path=='memberAsk'){
                return [
                    {genre:"",url:"member"},
                    {genre:"",url:"update"},
                    {genre:"",url:"memberAsk"},
                    {genre:"",url:"order"},
                ]
            }
        },
        changeroute (url) {
            this.$router.push('/home/'+ url)
            document.body.scrollTop = 0
            document.documentElement.scrollTop = 0
            //window.location.reload()
        }
    },
    beforeMount(){
    }
}
</script>

<style scoped>
.left{
    z-index:0;
    min-height: 20px;
    max-width:150px;
}
</style>



