<template>
    <v-container>
        <center>
        <v-card-text align="center" v-bind:key=item.title v-for="item in bar" v-if="item.path===path"><b><font size="6">{{item.title}}</font></b></v-card-text>
        </center>
        <v-card class="cardMid" align="center" v-if="path=='recommend'" >
            <recommend></recommend>
        </v-card>
        <v-card class="cardMid" align="center" v-else-if="path=='message'">
            <message></message>
        </v-card>
        <v-card class="cardMid" align="center" v-else-if="path=='site_map'">
            <sitemap></sitemap>
        </v-card>
        <v-card class="cardMid" align="center" v-else-if="path=='shopping'">
            <ask></ask>
        </v-card>
        <v-card class="cardMid" v-else-if="path=='introduction' || path=='history' || path=='envirnoment' || path=='records' || path=='license' || path=='product_report' || path=='report'">
            <about :path="path"></about>
        </v-card>
        <v-card class="cardMid" v-else-if="path=='register'">
            <register :path="path"></register>
        </v-card>
        <v-card class="cardMid" v-else-if="path=='construction' || path=='manufacture' || path=='electricity'">
            <industry :path="path"></industry>
        </v-card>
        <v-card class="cardMid" v-else-if="path=='lastest' || path=='DM'">
            <news :path="path"></news>
        </v-card>
        <v-card class="cardMid" v-else-if="path=='product_view'">
            <productview></productview>
        </v-card>
        <v-card class="cardMid" v-else-if="path=='article_view'">
            <articleview></articleview>
        </v-card>
        <v-card class="cardMid" v-else-if="path=='instruction' || path=='FAQ' || path=='note' || path=='theory' || path=='skill' || path=='conversion' || path=='data' || path=='t_link' || path=='issue'">
            <technology :path="path"></technology>
        </v-card>
        <v-card class="cardMid" v-else-if="path=='equipment' || path=='film' || path=='picture'">
            <scene :path="path"></scene>
        </v-card>
        <v-card class="cardMid" v-else-if="path=='relation' || path=='friend' || path=='national' || path=='develop'|| path=='governoment'||path=='source'||path=='commerce'||path=='work'||path=='rest'||path=='life'||path=='enterprise'||path=='association'">
            <relation :path="path"></relation>
        </v-card>
        <v-card class="cardMid" v-else-if="path=='brands'">
            <brand></brand>
        </v-card>
        <v-card class="cardMid" v-else-if="path=='information'">
            <information :path="path"></information>
        </v-card>
        <v-card class="cardMid" v-else-if="path=='member' || path=='order' || path=='memberAsk' || path=='update'">
            <member :path="path"></member>
        </v-card>
        <v-card class="cardMid" v-else-if="path=='pointer_pressure'||path=='pressure_switch'||path=='pressure_transmitter'||path=='transmitter'||path=='digit_pressure'||path=='pointer_thermometer'||path=='temperature_switch'||path=='digit_thermometer'||path=='TC_dipole'||path=='RTD_resistance'||path=='glass_thermometer'||path=='septum'||path=='vertify'||path=='accessory'||path=='FRL'||path=='valve'||path=='liquid'||path=='flow'||path=='adjuster'||path=='load_transmitter'||path=='newGoods' || path=='download' || path== 'temperature_transmitter' || path =='mechanical_switch'" >
            <product :path="path"></product>
        </v-card>
        <v-card class="cardMid" v-else-if="path=='login'">
            <login></login>
        </v-card>
    </v-container>
</template>

<script>
import api from '../store/api'
import pdf from 'vue-pdf'
export default {
    props:['path'],
    data(){
        return {
            bar:[
                {title:"",path:"introduction"},
                {title:"",path:"history"},
                {title:"",path:"envirnoment"},
                {title:"",path:"records"},
                {title:"/",path:"license"},
                {title:"",path:"product_report"},
                {title:"",path:"report"},
                {title:"",path:"download"},
                {title:"",path:"newGoods"},
                {title:"",path:"pointer_pressure"},
                {title:"",path:"pressure_switch"},
                {title:"()",path:"pressure_transmitter"},
                {title:"",path:"transmitter"},
                {title:"",path:"digit_pressure"},
                {title:"",path:"pointer_thermometer"},
                {title:"",path:"temperature_switch"},
                {title:"",path:"digit_thermometer"},
                {title:"TC",path:"TC_dipole"},
                {title:"RTD",path:"RTD_resistance"},
                {title:"",path:"glass_thermometer"},
                {title:"",path:"septum"},
                {title:"",path: "vertify"},
                {title:"",path: "accessory"},
                {title:"F.R.L 1~3",path:"FRL"},
                {title:"",path:"valve"},
                {title:"",path:"liquid"},
                {title:"",path:"flow"},
                {title:"",path:"adjuster"},
                {title:"",path:"load_transmitter"},
                {title:"",path:"brands"},
                {title:"",path:"construction"},
                {title:"",path:"manufacture"},
                {title:"",path:"electricity"},
                {title:"",path:"lastest"},
                {title:"E-DM",path:"DM"},
                {title:"",path:"instruction"},
                {title:"",path:"maintain"},
                {title:"FAQ",path:"FAQ"},
                {title:"",path:"note"},
                {title:"",path:"theory"},
                {title:"",path:"skill"},
                {title:"/",path:"conversion"},
                {title:"/",path:"data"},
                {title:"",path:"t_link"},
                {title:"",path:"issue"},
                {title:"/",path:"equipment"},
                {title:"",path:"film"},
                {title:"",path:"picture"},
                {title:"",path:"relation"},
                {title:"",path:"friend"},
                {title:"",path:"national"},
                {title:"",path:"develop"},
                {title:"",path:"governoment"},
                {title:"",path:"source"},
                {title:"",path:"commerce"},
                {title:"/",path:"work"},
                {title:"",path:"rest"},
                {title:"",path:"life"},
                {title:"",path:"enterprise"},
                {title:"",path:"association"},
                {title:"",path:"recommend"},
                {title:"Q&A",path:"Q&A"},
                {title:"",path:"message"},
                {title:"",path:"information"},
                {title:"",path:"site_map"},
                {title:"",path:"shopping"},
                {title:"",path:"member"},
                {title:"",path:"order"},
                {title:"",path:"update"},
                {title:"",path:"memberAsk"},
            ]
        }
    },
    beforeMount(){  
    },
}
</script>
<style>
.cardMid{
  max-width: 950px;
}
</style>




