<template>
    <v-container>
    <top class="top"></top>
    <v-layout class="mid" row>
        <v-flex>
            <v-layout column class="hidden-md-and-down">
              <v-btn color="light-blue lighten-5" block ripple v-bind:key=item.genre v-for="item in toolbar()" @click="changeroute(item.url)"><b>{{ item.genre }}</b></v-btn>
            </v-layout>
        </v-flex>
        <v-flex  offset-xs1>
            <br>
            <v-card>
                <center>
                    <v-card-text><b><font size=6>{{article.title}}</font></b></v-card-text>
                </center>
                <v-layout column="">
                    <pdf v-for="i in numPages" :key="i" :src="src" :page="i" style="display: inline-block; width: 100%"></pdf>
                </v-layout>
            </v-card>
        </v-flex>
        <v-flex class="hidden-md-and-down">
          <right></right>
        </v-flex>
    </v-layout>
    <br>
    <bottom></bottom>
  </v-container>
</template>

<script>
import api from '../../store/api'
import pdf from 'vue-pdf'

export default {
    props:['id'],
     components:{
        pdf,
    },
    data(){
        return{
            article:null,
            src: '',
            numPages: undefined
        }
    },
    methods: {
        toolbar : function (){
            if(this.article.classID==4 )
            {
                return [
                    {genre:"",url:"construction"},
                    {genre:"",url:"manufacture"},
                    {genre:"",url:"electricity"}]
            }
            if(this.article.classID==5 || this.article.classID==6)
            {
                return [
                    {genre:"",url:"lastest"},
                    {genre:"E-DM",url:"DM"},
                    {genre:"",url:"newGoods"}]
            }
            if(this.article.classID==7)
            {
                return [
                    {genre:"",url:"instruction"},
                    {genre:"FAQ",url:"FAQ"},
                    {genre:"",url:"note"},
                    {genre:"",url:"theory"},
                    {genre:"",url:"skill"},
                    {genre:"/",url:"conversion"},
                    {genre:"/",url:"data"},
                    {genre:"",url:"t_link"},
                    {genre:"",url:"issue"}]
            }
            if(this.article.classID==8)
            {
                return [
                    {genre:"/",url:"equipment"},
                    {genre:"",url:"film"},
                    {genre:"",url:"picture"}]
            }
            if(this.article.classID==9)
            {
                return [
                    {genre:"",url:"relation"},
                    {genre:"",url:"friend"},
                    {genre:"",url:"national"},
                    {genre:"",url:"develop"},
                    {genre:"",url:"governoment"},
                    {genre:"",url:"source"},
                    {genre:"",url:"commerce"},
                    {genre:"/",url:"work"},
                    {genre:"",url:"rest"},
                    {genre:"",url:"life"},
                    {genre:"",url:"enterprise"},
                    {genre:"",url:"association"}]
            }
        },
         changeroute (url) {
            this.$router.push('/home/'+ url)
            document.body.scrollTop = 0
            document.documentElement.scrollTop = 0
        },
        loadingTask(url) {
            return pdf.createLoadingTask(url);
        }
    },
    beforeMount(){
        let self = this;
        api.getArticle(this.id).then( (res) => {
            self.article = res.data.article;
            console.log(self.article.context)
            self.src = this.loadingTask(self.article.context);
            return self.src;
        }).then(pdf => {
            self.numPages = pdf.numPages;
        }).catch(error=>{
            console.log(error);
        })
    }
}
</script>

<style scoped>
.top {
    position: relative;
    z-index: 100;
}

.mid {
    position: relative;
    z-index: 0;
}
</style>



